import React from 'react';
import './Navbar.css'
import {onClickAddNewFriend} from '../../Redux/sidebarReducer.js'
import Navbar from "./Navbar";
import {connect} from "react-redux";
import {FriendChange} from "../../Redux/sidebarReducer";


let mapStateToProps = (state) => {
    return {
        Friend: state.sidebar.Friend
    }
};

let NavbarContainer = connect(mapStateToProps, {
    onClickAddNewFriend,
    FriendChange
})(Navbar);

export default NavbarContainer;

