import {usersAPI} from "../DAL_api/api";
import {mapOfReducer} from "../utitlits/mapOfReducer";

const FOLLOW = "FOLLOW";
const UNFOLLOW = "UNFOLLOW";
const LOAD_USERS = "LOAD-USERS";
const SET_ACTIVE_PAGE = "SET_ACTIVE_PAGE";
const SET_TOTAL_COUNT = "SET_TOTAL_COUNT";
const TOOGLE_PRELOADER_USERS = 'TOOGLE_PRELOADER_USERS';
const TOOGLE_FOLLOWING_PROGRESS = 'TOOGLE_FOLLOWING_PROGRESS';
const TOOGLE_PAGES_TEN = "TOOGLE_PAGES_TEN";

const TOOGLE_PAGES_TEN_NULL = "TOOGLE_PAGES_TEN_NULL";

let initialState = {
    users: [],
    countUsers: 10,
    activePage: 1,
    totalCount: 0,
    isFetching: false,
    followingInProgress: [], //создаем пустой массив
};

const usersReducer = (state = initialState, action) => {
    switch (action.type) {
        case FOLLOW:
            return {
                ...state,
                users: mapOfReducer(state.users, action.userId, "id", {followed: true})
            };
        case UNFOLLOW:
            return {
                ...state,
                users: mapOfReducer(state.users, action.userId, "id",{followed: false})
            };
        case LOAD_USERS:
            return {
                ...state,
                users: action.users
            };
        case SET_ACTIVE_PAGE:
            return {
                ...state,
                activePage: action.page
            };
        case SET_TOTAL_COUNT:
            return {
                ...state,
                totalCount: action.totalCount
            };
        case TOOGLE_PRELOADER_USERS:
            return {
                ...state,
                isFetching: action.isFetching
            };
        case TOOGLE_FOLLOWING_PROGRESS:
            return {
                ...state,
                followingInProgress: action.followingInProgress
                    ? [...state.followingInProgress, action.id]
                    : state.followingInProgress.filter(id => id !== action.id)
            };
        default:
            return state;
    }
};


export const onClickFollow = (userId) => ({type: FOLLOW, userId: userId});
export const onClickUnFollow = (userId) => ({type: UNFOLLOW, userId: userId});
export const loadUsers = (users) => ({type: LOAD_USERS, users: users});
export const onClickSetPage = (page) => ({type: SET_ACTIVE_PAGE, page});
export const setTotalUserCounts = (totalCount) => ({type: SET_TOTAL_COUNT, totalCount});
export const tooglePreloader = (isFetching) => ({type: TOOGLE_PRELOADER_USERS, isFetching});
export const toogleFolowingProgress = (followingInProgress, id) => ({type: TOOGLE_FOLLOWING_PROGRESS, followingInProgress, id});


export const GetUsersThunkC = (activePage, countUsers) => async(dispatch) => {

    dispatch(tooglePreloader(true));

    let data = await usersAPI.getUsers(activePage, countUsers);
    dispatch(tooglePreloader(false));
    dispatch(loadUsers(data.items));
    dispatch(setTotalUserCounts(data.totalCount));
    dispatch(onClickSetPage(activePage));
};


export const getFollowThunkC = (id) => async(dispatch) => {
    FollowUnfollow(dispatch, usersAPI.usersFollow.bind(usersAPI), onClickFollow, id)
    // передаем разную логику
};


export const getUnFollowThunkC = (id) => async(dispatch) => {
    FollowUnfollow(dispatch, usersAPI.usersUnFollow.bind(usersAPI), onClickUnFollow, id)
    // передаем разную логику
};


const FollowUnfollow = async(dispatch, method, action, id) => { // вынесли сюда общую логику
    dispatch(toogleFolowingProgress(true, id));
    let data = await method(id)
    if (data.resultCode === 0)
        dispatch(action(id));
    dispatch(toogleFolowingProgress(false, id));
};







export default usersReducer;