import {connect} from "react-redux";
import {
    onClickFollow,
    onClickUnFollow,
    onClickSetPage,
    toogleFolowingProgress,
    GetUsersThunkC,
    getFollowThunkC,
    getUnFollowThunkC,
} from "../../Redux/usersReducer";
import React from "react";
import Users from "./Users";
import Prealoder from "../Common/Preloader/Preloader";
import {
    GetActivePage,
    GetCountUsers,
    GetFollowingInProgress,
    GetIsFetching, GetNumbersPages, GetNumbersPagesNull,
    GetTotalCount,
    GetUsersSelector
} from "../../Redux/users-selector";

class UsersContainer extends React.Component {
    componentDidMount() {
        let {activePage, countUsers} = this.props;
        this.props.GetUsersThunkC(activePage, countUsers);
    }

    onChangePage = (page) => {
        this.props.GetUsersThunkC(page, this.props.countUsers);
    };

    render() {
        debugger
        return (
            <>
                {this.props.isFetching ? <Prealoder/> : null}
                <Users onChangePage={this.onChangePage}
                       totalCount={this.props.totalCount}
                       countUsers={this.props.countUsers}
                       activePage={this.props.activePage}
                       users={this.props.users}
                       followingInProgress={this.props.followingInProgress}
                       getFollowThunkC={this.props.getFollowThunkC}
                       getUnFollowThunkC={this.props.getUnFollowThunkC}

                />)
            </>
        )
    }
}

let mapStateToProps = (state) => {
    return {
        users: GetUsersSelector(state),
        countUsers: GetCountUsers(state),
        totalCount: GetTotalCount(state),
        activePage: GetActivePage(state),
        isFetching: GetIsFetching(state),
        followingInProgress: GetFollowingInProgress(state),
    }
};





export default connect(mapStateToProps, {
    onClickFollow,
    onClickUnFollow,
    onClickSetPage,
    toogleFolowingProgress,
    GetUsersThunkC,
    getFollowThunkC,
    getUnFollowThunkC,
})(UsersContainer);
