import {Field, reduxForm} from "redux-form";
import React from "react";
import {createField, InputContainer} from "../../Common/TextareaContainer/TextareaContainer";
import {maxlength, requiredField} from "../../../validation/validationForm";

let maxlength50 = maxlength(50);

const LoginForm = (props) => {
    return(
        <form onSubmit={props.handleSubmit}> {/*handleSubmit вызывается при клике на ЛОГИН кнопку*/}
            {createField('email', InputContainer, 'email', [requiredField, maxlength50])}
            {createField('password', InputContainer, 'password', [requiredField, maxlength50], {type: 'password'})}
            {createField(null, InputContainer, 'remember', null, {type: 'checkbox'}, 'remember me')}
            <div className="">
                {props.error}
            </div>
            <button>LOGIN</button>
        </form>
    )
};

const ReduxLoginForm = reduxForm({
        form: 'login'
}
)(LoginForm);

export default ReduxLoginForm;

