import React from "react";
import './mains.css';
import {Field} from "redux-form";
import {requiredField} from "../../../validation/validationForm";

export const TextareaContainer = (props) => {
    const {input, meta, child, ...restProps} = props;
    return <TextContainer {...props}><textarea {...input} {...restProps}/></TextContainer>
};


export const InputContainer = (props) => {
    const {input, meta, child, ...restProps} = props;
    return <TextContainer {...props}><input {...input} {...restProps}/></TextContainer>
};


export const TextContainer  = ({input, meta, child, ...props}) => {
    let hasError = meta.touched && meta.error;
    return (
        <div className={'formControl'}>
            <div className={hasError ? 'formControl_Error' : ''}>
                {props.children}
            </div>
            {hasError ? <p className="formControl_Error">{meta.error}</p> : ''}
        </div>
    )
};


export const createField = (placeholder, component, name, validates, props, text = '') => {
    return (
        <div className="">
            <Field placeholder={placeholder}
                   component={component}
                   name={name}
                   validate={validates}
                   {...props}/> {/*Всякие пропсы, которые будут не у всех, например: type*/}
            {text} {/*тоже есть не у всех*/}
        </div>
    )
};