const ADD_FRIEND = "ADD-FRIEND";
const FRIEND_CHANGE = 'FRIEND-CHANGE';
 //создаем обьект, чтобы при первом запуске state был не пустой, поэтому передаем по умолчанию
let initialState = {
	Friend: [
		{id: 1, name: "dima", src: "https://go3.imgsmail.ru/imgpreview?key=2d8734305044f458&mb=imgdb_preview_674"},
		{id: 2, name: "leha", src: "https://go3.imgsmail.ru/imgpreview?key=2d8734305044f458&mb=imgdb_preview_674"},
		{id: 3, name: "ramil", src: "https://go3.imgsmail.ru/imgpreview?key=2d8734305044f458&mb=imgdb_preview_674"}
	]
};

// первый раз по умолчанию, а дальше он сам будет закидывать state
const sidebarReducer = (state = initialState, action) => {
	switch(action.type) {
		case ADD_FRIEND:
			let person = {
				name: action.name,
				src: "https://go3.imgsmail.ru/imgpreview?key=f3b8c0272961b02&mb=imgdb_preview_69"
			};
			return {
				...state,
				Friend: [...state.Friend, person]
			};
		case FRIEND_CHANGE:
			let newFriend = {
				id: 4,
				name: "Carl",
				src: "https://go3.imgsmail.ru/imgpreview?key=2d8734305044f458&mb=imgdb_preview_674"
			};
			return {
				...state,
				Friend: state.Friend.map(friend => {
					if (friend.id === action.id) {
						return newFriend;
					}
					return friend;
				})
			};
		default: 
			return state;
	}
}

export const onClickAddNewFriend = (name) => ({type: ADD_FRIEND, name: name});
export const FriendChange = (id) => ({type: FRIEND_CHANGE, id: id});

export default sidebarReducer;