import React from 'react';
import './Header.css'
import {NavLink} from "react-router-dom";

const Header = (props) => {
	return(
		<header>
			<div className="container">
        		<img src="https://upload.wikimedia.org/wikipedia/commons/thumb/e/e7/Instagram_logo_2016.svg/768px-Instagram_logo_2016.svg.png" alt=""/>
        	</div>
			<div>
				{props.isAuth ? <NavLink to={'/login'}>{props.login} - <button onClick={props.setLogOutDataThunkCreator}>Exit</button></NavLink>
					: <NavLink to={'/login'}>Login</NavLink>
				}
			</div>
      	</header>
	)
}

export default Header;