import React from 'react';
import './Header.css'
import Header from "./Header";
import {connect} from "react-redux";
import {setLogOutDataThunkCreator} from "../../Redux/authReducer";
import Prealoder from "../Common/Preloader/Preloader";

class HeaderContainer extends React.Component {

	render() {
		return (
			<>
				{this.props.isFetching ? <Prealoder/> : null}
				<Header {...this.props}/>
			</>
		)
	}
}


const mapStateToProps = (state) => {
	return {
		isAuth: state.auth.isAuth,
		login: state.auth.login,
		isFetching: state.auth.isFetching
	}
};

export default connect(mapStateToProps, {setLogOutDataThunkCreator})(HeaderContainer);