import React, {useState} from "react";
import rightArrow from '../../Assets/multimedia.png'


export const Paginator = (props) => {
    let pageCount = Math.ceil(props.totalCount / props.countUsers);
    let pages = [];

    let portionItem = Math.ceil(pageCount / props.portionSize);
    debugger
    const [portionNumber, SetPortionNumber] = useState(1); // изначально первая страница
    let leftBorderPortion = (portionNumber - 1) * props.portionSize + 1; // формула для вычисления самой левой крайней страницывы этой порции
    let rightBorderPortion = portionNumber * props.portionSize; // формула для вычисления самой правой крайней страницывы этой порции
    for (let i = 1; i <= pageCount; i++) {
        pages.push(i)
    }
    /*const toogleTenPagesBack = () => {
        let summ2 = pageCount - props.numbersPagesNull;
        debugger
        if(summ2 < item_page) {
            props.tooglePagesTen((props.numbersPages - summ2));
            props.tooglePagesTenNull((props.numbersPagesNull - item_page))
        }
        else if (props.numbersPagesNull > (item_page - 1)) {
            props.tooglePagesTen((props.numbersPages - item_page));
            props.tooglePagesTenNull((props.numbersPagesNull - item_page))
        }
    };

    const toogleTenPagesUp = () => {
        if ((props.numbersPages + item_page) < pageCount) {
            props.tooglePagesTen((props.numbersPages + item_page));
            props.tooglePagesTenNull((props.numbersPagesNull + item_page))
        }
        else {
            props.tooglePagesTen(((pageCount - props.numbersPages) + props.numbersPages));
            props.tooglePagesTenNull((props.numbersPagesNull + item_page));
        }
    };*/


    return(
        <div className='paginator'>
            {portionNumber > 1 ?
                <button onClick={() => SetPortionNumber(portionNumber - 1)}>DOWN</button> : null
            }
            {pages.filter(portion => (portion >= leftBorderPortion && portion <= rightBorderPortion)).map(page =>
                <span className={(props.activePage === page ? 'selected' : '') +' hellos' } onClick={() => props.onChangePage(page)}>
                    {page}
                </span>)
            }
            {portionItem > portionNumber ?
                <button onClick={() => SetPortionNumber(portionNumber + 1)}><img src={rightArrow} alt=""/></button> : null
            }
        </div>
    )
}