import {profileAPI} from "../DAL_api/api";

const ADD_POST = "ADD_POST";
const SET_USER_PROFILE = "SET_USER_PROFILE";
const SET_CONTACT = "SET_CONTACT";
const IS_FETCHING = "IS_FETCHING";
const SET_STATUS = "GET_STATUS";
const DELETE_POST = "DELETE_POST";


let initialState = {
	PostData: [
		{message: "Hello, its top", count: 3, id: 1},
		{message: "Im Denis and Im fine", count: 15, id: 2},
		{message: "Its nice", count: 10, id: 3},
		{message: "I love you!", count: 48, id: 4},
		{message: "Hello, its top", count: 3, id: 5},
		{message: "Im Denis and Im fine", count: 15, id: 6},
		{message: "Its nice", count: 10, id: 7},
		{message: "I love you!", count: 48, id: 8}
	],
	ProfileData: [],
	socialNetworks: [],
	isFetching: false,
	status: ""
};

const profileReducer = (state = initialState, action) => {
	switch(action.type) {
		case DELETE_POST: {
			return {
				...state,
				PostData: state.PostData.filter(p => p.id !== action.id)
			}
		}
		case ADD_POST: {
			let newPost = {
				id: 5,
				message: action.newPostText,
				count: 0
			};
			return {
				...state,
				PostData: [...state.PostData, newPost],
			}
		}
		case SET_USER_PROFILE: {
			return {
				...state,
				ProfileData: action.ProfileData
			}
		}
		case SET_CONTACT:
			return {
				...state,
				socialNetworks: [
					{name: 'facebook', puthNetwork: action.contacts.facebook},
					{name: 'website', puthNetwork: action.contacts.website},
					{name: 'vk', puthNetwork: action.contacts.vk},
					{name: 'twitter', puthNetwork: action.contacts.twitter},
					{name: 'instagram', puthNetwork: action.contacts.instagram},
					{name: 'youtube', puthNetwork: action.contacts.youtube},
					{name: 'github', puthNetwork: action.contacts.github},
					{name: 'mainLink', puthNetwork: action.contacts.mainLink},
				]
			};
		case IS_FETCHING:
			return {
				...state,
				isFetching: action.isFetching
			};
			//2. Cоздаем ветки в редюсере
		case SET_STATUS:
			return {
				...state,
				status: action.status
			};
		default: 
			return state;
	}
};

export const addPost = (newPostText) => ({type: ADD_POST, newPostText});
export const setUserProfile = (ProfileData) =>  ({type: SET_USER_PROFILE, ProfileData});
export const setContact = (contacts) =>  ({type: SET_CONTACT, contacts});
const isFetchingData = (isFetching) =>  ({type: IS_FETCHING, isFetching});
export const setStatus = (status) => ({type: SET_STATUS, status});
export const deletePost = (id) => ({type: DELETE_POST, id});



export const getProfileUserThunkCreation = (userId) => async(dispatch) => {

	dispatch(isFetchingData(true));

	let data = await profileAPI.getProfile(userId)
	dispatch(setContact(data.contacts));
	dispatch(setUserProfile(data));

	dispatch(isFetchingData(false))
};
export const setProfileStatusThunkCreator = (userId) => async(dispatch) => {
	let response = await profileAPI.getProfileStatus(userId)
	dispatch(setStatus(response.data))
};
export const updateProfileStatusThunkCreator = (status) => async(dispatch) => {
	let data = await profileAPI.updateStatus(status)
	if (data.resultCode === 0)
		dispatch(setStatus(status));
};


export default profileReducer;