import {authAPI} from "../DAL_api/api";
import {getUserDataThunkCreator} from "./authReducer";

const SET_INITIALIZING = "INITIALIZING";



let initialState = {
    initializing: null,
};

const appReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_INITIALIZING:
            return {
                ...state,
                initializing: true,
            };
        default:
            return state;
    }
};

export const setInizializing = () =>  ({type: SET_INITIALIZING});

export const InizializingThunk = () => (dispatch) => {
    let init = dispatch(getUserDataThunkCreator());
    Promise.all([init])
        .then(() => {
            dispatch(setInizializing())
        });
};


export default appReducer;

