import React from 'react'
import './Friend.css'
const Friend = (props) => {
	let onFriendChange = () => {
		props.FriendChange(props.id)
	};

	return (
		<div className="best-friends__friend" onClick={onFriendChange}>
	        <div className=""><img src={props.src} alt=""/></div>
	        <p>{props.name}</p>	  
	    </div>
	)
}

export default Friend;