import {authAPI} from "../DAL_api/api";
import {stopSubmit} from "redux-form";

const SET_USER_DATA = "SET_USER_DATA";
const IS_FETCHING_AUTH = 'IS_FETCHING_AUTH';
/*const IS_FETCHING_AUTH = 'IS_FETCHING_AUTH';*/


let initialState = {
    id: null,
    email:  null,
    login: null,
    isFetching: false,
    isAuth: false,
    captcha: 0
};

const authReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_USER_DATA:
            debugger
            return {
                ...state,
                ...action.userData,
            };
        case IS_FETCHING_AUTH:
            return {
                ...state,
                isFetching: action.isFetching
            };
        default:
            return state;
    }
};


export const setUserData = (id, email, login, isAuth) => ({type: SET_USER_DATA, userData: {id, email, login, isAuth}});
export const isFetchingData = (isFetching) =>  ({type: IS_FETCHING_AUTH, isFetching});

export const getUserDataThunkCreator = () => async(dispatch) => {
    dispatch(isFetchingData(true));
    let data = await  authAPI.setUserData();
        if (data.resultCode === 0) {
            dispatch(setUserData(data.data.id, data.data.email, data.data.login, true));
        }
        dispatch(isFetchingData(false))
};


export const setLoginDataThunkCreator = (login, password, rememberMe) => async(dispatch) => {
    let data = await authAPI.setLogin(login, password, rememberMe);
        if (data.resultCode === 0)
            dispatch(getUserDataThunkCreator());
        else {
            let mes = data.messages.length > 0 ? data.messages[0] : 'Неизвестная ошибка';
            dispatch(stopSubmit('login', {_error: mes}))
        }
};

export const setLogOutDataThunkCreator = (login, password, rememberMe) => async(dispatch) => {
    debugger
    let data = await authAPI.setLogOut();
        if (data.resultCode === 0)
            dispatch(setUserData(null, null, null, false)); // выходим и зануляем се
};

export default authReducer;