import React, {Suspense} from 'react';
import {Redirect} from "react-router-dom";
import {connect} from "react-redux";
import Prealoder from "../Component/Common/Preloader/Preloader";


let mapStateToPropsForRedirect = (state) => ({
    isAuth: state.auth.isAuth
});

export const withRedirect = (Component) => { //HOC


    let containerComponent = (props) => { //КОНТЕЙНЕРНАЯ КОМПОНЕНТА

        if (!props.isAuth) //ЕСЛИ НЕ АВТОРИЗОВАН, ТО РЕДИРЕКТ
            return <Redirect to={'/login'}/>;
        return <Component {...props}/> //ЕСЛИ АВТОРИЗОВАН ВОЗВРАЩАЕМ ОТРИСОВАННУЮ КОМПОНЕНТУ
    };

    let ConnectedRedirectComponent = connect(mapStateToPropsForRedirect)(containerComponent); // снабжает нашу компоненту isAuth


    return ConnectedRedirectComponent; //ВОЗВРАЩАЕМ КОНТЕЙНЕР c isAuth
};


export const withSuspence = (Component) => {
    const SuspenseContainer = (props) => {
        return (
            <Suspense fallback={<Prealoder/>}>
                <Component {...props}/>
            </Suspense>
        )
    };
    return SuspenseContainer
};