import * as axios from "axios";

let instance = axios.create({
    withCredentials: true,
    baseURL: 'https://social-network.samuraijs.com/api/1.0/',
    headers: {
        "API-KEY": "a879937b-3f80-43ec-ab63-7a5a60b364bb"
    }
});

export const usersAPI = {
    getUsers(activePage, countUsers) {
        return instance.get(`users?page=${activePage}&count=${countUsers}`)
            .then(response => response.data)
    },
    usersUnFollow(id) {
        return instance.delete(`follow/` + id).then(response => response.data)
    },
    usersFollow(id) {
        return instance.post(`follow/` + id).then(response => response.data)
    }
};

export const authAPI = {
    setUserData() {
        return instance.get(`auth/me`).then(response => response.data)
    },

    setLogin(email, password, rememberMe = false) {
        return instance.post('auth/login', {email, password, rememberMe}).then(response => response.data)
    },

    setLogOut() {
        return instance.delete('auth/login').then(response => response.data)
    },
};

export const profileAPI = {
    getProfile(userId) {
        return instance.get('profile/' + userId).then(response => response.data)
    },

    getProfileStatus(userId) {
        return instance.get('profile/status/' + userId)
    },
    updateStatus(text) {
        return instance.put('profile/status', {status: text}).then(response => response.data)
    }
};