import React from 'react';
import ReduxLoginForm from "./LoginForm/LoginForm";
import {connect} from "react-redux";
import {setLoginDataThunkCreator} from "../../Redux/authReducer";
import {Redirect} from "react-router-dom";


const Login = ({setLoginDataThunkCreator, isAuth}) => {

    const onSubmit = (formData) => {
        setLoginDataThunkCreator(formData.email, formData.password, formData.remember);
        console.log(formData)
    };

    if (isAuth) {
        return <Redirect to={'/profile'}/>
    }

    return (
        <section>
            <h1>LOGIN</h1>
            <ReduxLoginForm onSubmit={onSubmit}/>
        </section>
    )
};

let mapStateToProps = (state) => ({
        isAuth: state.auth.isAuth
    }
);


export default connect(mapStateToProps, {setLoginDataThunkCreator})(Login)
