import {createSelector} from "reselect";

export const GetUsers = (state) => {
    return state.usersPage.users
};

export const GetUsersSelector = createSelector(GetUsers, (users) => {
    return users.filter(u => true)
});

export const GetCountUsers = (state) => {
    return state.usersPage.countUsers
};


export const GetTotalCount = (state) => {
    return state.usersPage.totalCount
};

export const GetActivePage = (state) => {
    return state.usersPage.activePage
};


export const GetIsFetching = (state) => {
    return state.usersPage.isFetching
};


export const GetFollowingInProgress = (state) => {
    return state.usersPage.followingInProgress
};

export const GetNumbersPages = (state) => {
    return state.usersPage.numbersPages
};

export const GetNumbersPagesNull = (state) => {
    return state.usersPage.numbersPagesNull
};