import React, {Suspense} from 'react';
import './App.css';
import {BrowserRouter, Route, withRouter} from 'react-router-dom';
import NavbarContainer from "./Component/Navbar/NavbarContainer";
import UsersContainer from "./Component/Users/usersContainer";
//import ProfileContainer from "./Component/Profile/ProfileContainer";
import HeaderContainer from "./Component/Header/HeaderContainer";
import Login from "./Component/Login/Login";
import {compose} from "redux";
import {connect, Provider} from "react-redux";
import {InizializingThunk} from "./Redux/appReducer";
import Prealoder from "./Component/Common/Preloader/Preloader";
import store from "./Redux/reduxStore";
import {withSuspence} from "./HOC/withRedirect";

//import DialogsContainer from './Component/Dialogs/DialogsContainer.js';
const DialogsContainer = React.lazy(() => import('./Component/Dialogs/DialogsContainer.js'));
const ProfileContainer = React.lazy(() => import('./Component/Profile/ProfileContainer'));
class App extends React.Component {

    componentDidMount() {
        this.props.InizializingThunk();
    }

    render() {
        if (!this.props.initializing)
            return <Prealoder/>
        return (
            <div className="App">
                <HeaderContainer/>
                <div className="container">
                    <NavbarContainer/>
                    <div className="content">
                        <Route path="/profile/:userId?" render={withSuspence(ProfileContainer)}/>
                        <Route path="/dialogs" render={withSuspence(DialogsContainer)}/>
                        <Route path="/users" render={() => <UsersContainer/>}/>
                        <Route path="/login" render={() => <Login/>}/>
                    </div>
                </div>
            </div>
        );
    }
}

let mapStateToProps = (state) => {
    return {
        initializing: state.app.initializing
    }
};

const AppContainer = compose(
    connect(mapStateToProps, {InizializingThunk}),
    withRouter)
(App);


export const RenderApp = () => {
    return (
        <BrowserRouter>
            <Provider store={store}>
                <AppContainer/>
            </Provider>
        </BrowserRouter>
    )
};
