import React from 'react';
import userPhotoDefault from "../../Assets/userPhotoDefault.png";
import {NavLink} from "react-router-dom";


let User = ({user, followingInProgress, getUnFollowThunkC, getFollowThunkC}) => {
    return (
        <div>
            <div className="">
                <div className="">
                    <NavLink to={'/profile/' + user.id}>
                        <img src={user.photos.small ? user.photos.small : userPhotoDefault} alt=""/>
                    </NavLink>
                </div>
                {user.followed === true ?
                    <button disabled={followingInProgress.some(number => number === user.id)}
                            onClick={() => getUnFollowThunkC(user.id)}>UNFOLLOWED
                    </button> :
                    <button disabled={followingInProgress.some(id => id === user.id)}
                            onClick={() => getFollowThunkC(user.id)}>
                        FOLLOWED
                    </button>
                }
            </div>
            <div className="">
                <div className="">
                    <p>{user.name}</p>
                    <p>{user.status}</p>
                </div>
            </div>
        </div>
    )
};

export default User;