


export let maxlength = (length) => (value)  => {
    if (value && value.length > length)
        return `Вы ввели больше ${length} символов`
    return undefined;
};

export let requiredField = (value) => { // обязательное поле
    if (!value)
        return 'Вы ничего не ввели';
    return undefined
};