import React from 'react';
import './Navbar.css'
import {NavLink} from "react-router-dom";
import Friend from './Friend/Friend.js'


const Navbar = (props) => {

	let onClickaddNewFriend = () => {
		props.onClickAddNewFriend('Denis')
	};

	let Friends = props.Friend.map(friend => <Friend FriendChange={props.FriendChange} id={friend.id} name={friend.name} src={friend.src}/>);
	return (
		<nav>
	        <ul>
	          <li><NavLink to="/profile">Profile</NavLink></li>
	          <li><NavLink to="/dialogs">Messages</NavLink></li>
	          <li><NavLink to="/users">users</NavLink></li>
	          <li><NavLink to="/jhh">Profile</NavLink></li>
	          <li><NavLink to="/jhh">Profile</NavLink></li>
	        </ul>
	        <div className="best-friends">
	        	{Friends}
	        </div>
			<div><button onClick={onClickaddNewFriend}>ADD FRIEND</button></div>
     	</nav>
	)
}

export default Navbar;

